.descriptionbox{
    /* margin: 120px 170px; */
    margin: 100px 70px;
}

.descriptionbox-navigator{
    display: flex;
}

.descriptionbox-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 170px;
    height: 70px;
    border: 1px solid black;
}

.descriptionbox-nav-box.fade {
    background: #FBFBFB;
}

.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #D0D0D0;
    padding: 48px;
    padding-bottom: 70px;
}

@media (max-width: 1280px) {
    .descriptionbox{
        margin: 60px;

    }
}

@media (max-width: 800px) {
    .descriptionbox{
        margin: 60px 20px;
    }
    .descriptionbox-description{
        font-size: 14px;
    }
}
